ul{
  list-style: none;
  display: flex;
}
li{  
  margin: 0px 20px 0px 20px;
}
body { 
  margin: 0;
  line-height: 1.75;
  font-weight: 400;
  font-family: sans-serif;
  color: #747474;
}
a {
  text-decoration: none;
  
}
.active{
  color: #00b39b;
}
header{ 
  background-color: white;
   width:100%;
  position: fixed;
  top: 0;
  left: 0;
padding: 20px 0px;
  
   border: 1px black;
  height: 50px;

  display: flex;
  justify-content: space-around;
}
header a{
  color: #ababab;
}
header img{
  width: 268px;
  height: 33px;
}
/*****************************************section services-**/
section#services {
  margin-top: 100px;
}
#sevices{
  margin-top: 100px;
  display: flex;
 flex-direction: column; 
 align-items: center;
}
#services h1 {
  margin-bottom: 50px;
  text-align: center;
  color: #00b39b ;
}
#services img{
  display: block;
  margin-left: auto;
  margin-right:auto ;

}
.paragraphes{
  margin-left: 100px;
  margin-right: 100px;
  display: flex;
  justify-content: space-around;
  

}
.paragraphes p{
  align-items: center;
  
display: block;

}
.cards{
  position: relative;
   margin-left: 100px;
  margin-right: 100px;
  display: flex;
  justify-content: space-around;
}
.cards h3{

  font-size: .875em;
  font-weight: 400;
  margin: 0;
}
.cards h2{
  font-size: 1.625em;
  
  margin-top: .3em;
}
.cardPatient{ 
  background-color: #00b39b;
}
.cardDoctors{
  background-color: #3d83df;
}
.card{
   overflow: hidden;
   margin: 10px;
  color :white;
  padding: 20px;
  border-radius: 10px;

}
.slct{
  padding: 10px 10px;
  border-radius: 5px;
}
.photocard{ 
  margin-left: 40% !important;
  max-width: 100%;
  height: auto;
}
/***********************************healthcareplatform******************************/

#healthcarePlatform{
  padding:50px;
  display: flex;
  height: 800;
  background-color: #e5faf8;

}
#healthcarePlatform h1{
  font-size: 2.125rem;
  color: #00b39b
}
.healthcarePlatform{
  display: flex;
  
}

.Performances h3{
  
  color:#00b39b ;
}
.Performances{
  width: 50%;
  display: flex;
  flex-flow: row wrap;
}
.healthcarePlatformPartleft{
  width: 50%;

}
.performancebox{
  border-radius: 10px;
  padding: 10px;
  text-align: center;
  margin: 10px;
  width: 40%;
  border:1px solid ;
  background-color: white;
}
.specialbox{
  margin-top: 40px;
}
.specialbox2{
  margin-bottom: 40px;
}
/************************************logos******************************************/
#logos{
  display: flex;
}
#logos a path{
  fill: gray;
}
#logos a:hover path {
  fill: #00bfa5;
}

/******************************imagessection************************************/
.images {
  margin-right: 30px;
  margin-top: 30px ;
  display: flex;
  flex-flow: row wrap ;
justify-content: center;
} 
.photos{
  border: .5px solid gray;
  padding-bottom :30px ;

}
.menu{ 
  display: none;
  position: absolute;
}
li.depart{
  position: relative;
}
li.depart:hover .menu { 
  display: block; 
}
.submenu{
  display: block;
  padding-left: 0;
}
.submenu li {
  margin-left: 0;
}
footer{
  text-align: center;
}
.images a:hover{
  box-shadow: 10px 5px grey;
}
.images a{
  margin: 30px;
}
.photos{
  margin: 0;
}
#images h1,p{
  margin-top:50px;
  margin-left: 50px;
  
  text-align: center;
  
}
button{
  background-color: #3d83df;
  color: white;
  float: right;
  margin: 10px;
  border: none;
  border-radius: 5px;
}
span{
  color: grey;
  margin:10px ;
}

